import React from 'react'
import { FormattedMessage } from 'react-intl'
import './index.css'

const MainAresConnect = () => (
  <div className="container p-3 p-md-5 main-content">
    <div className="display-table">
        <div className="table-cell">
          <div className="connect-headerLeft">
              <div className="arena-brand">
                <div className="icon-box">
                <img src="/images/icon-connect.png" width="60%"  alt=""/>
                </div>
              </div>
              <ul className="nav navbar-nav">
                <li className="AresConnect-text">
                <div>
                   <FormattedMessage id="ares"/>
                   <FormattedMessage id="connect"/>
                   </div>   
                </li>
              </ul>
          </div>
        </div>
        </div>
    </div>
)

export default MainAresConnect



