	  'askares.title': 'Ask Ares',
	  'askares.subtitle': 'Some commonly asked questions',
	
	  'faq.title1': 'Whats the difference between ares and other platforms?',
	  'faq.title2': 'Why battle platfrom on blockchain is better for gamer?',
	  'faq.title3': 'How Ares change the blockchain gaming experence?',
	  'faq.title4': 'How gamers avatar and artifacts work?',
	  'faq.title5': 'How to get benefit from blockchain games?',
	  'faq.title6': 'How to buy Ares Token?',
	
	  'faq.subtitle1': 'Ares is committed to providing players with a platform based on blockchain fairness and asset attribution, especially for casual games, intellectual games and other types of optimization, unlike the blockchain game distribution platform (App Store) in the usual sense. Players can participate in or build their own challenges with others, which avoids the tendency of the platform to be centralized and other things.',
	  'faq.subtitle2': 'Because it is a fair, justice, open game environment and introduces the attribution of game props as well as the player\'s image is in the games. The fairness between players, the flow of funds and the openness of the game, the fairness of game design are all guaranteed on the blockchain.',
	  'faq.subtitle3': 'Ares offers a different battle experience for many types of players. Such as limited time arena, chess room, multiplayer casual battles.',
	  'faq.subtitle4': 'The player image is a Token protocol in the form of an ERC998 protocol, which can be combined with the ERC721 protocol of the props to further bind the player\'s rights to the chain.',
	  'faq.subtitle5': 'Developers can use the decentralized database storage provided by Ares Suite SDK, player image and asset access, random number generation and other interfaces to complete the whole process of traditional casual game on blockchain in one stop. The SDK also provides a cross-chain protocol to ensure that players can log into multiple mainstream public chains at the same time.',
	  'faq.subtitle6': 'You can purchase directly through Ares Wallet or the official website, or buy from the platform and the exchange. The release time is pending.',
	
	  'jointeam.title': 'Join our Team',
	  'jointeam.subtitle': 'Are you a right fit for Ares?',
	
	  'jointeamfaq1.title': 'Smart Contract Developer',
	  'jointeamfaq1.subtitle': 'This full time position (40h) is required to be onsite at our Berlin office. Remote work is\n' + '\n' + 'only available for short term.\n' + '\n' + 'We are looking for an exceptional Smart Contract developer to work with our crossfunctional\n' + '\n' + 'team, and join our community of talented experts.\n' + '\n' + 'About you: \n' + '\n' + 'You will be responsible for the integration of Solidity smart contracts.\n' + 'Contribute to and work within Agile and DevOps frameworks.\n' + 'Develop in alignment with overall architecture design.\n' + 'Experience of developing Ethereum smart contracts written in Solidity,\n' + ' full-stack development, especially with ReactJS and Node, is an\n' + 'advantage',
	
	  'jointeamfaq2.title': 'Game Developer',
	  'jointeamfaq2.subtitle': 'This full time position (40h) is required to be onsite at our Berlin office. Remote work is\n' + '\n' + 'only available for short term.\n' + '\n' + 'The Game Developer role (start with HTML5) is responsible for designing, architecting,\n' + '\n' + 'testing and implementing game features into existing or own games which use\n' + '\n' + 'blockchain technology. You will work closely with external game artists and producers\n' + '\n' + 'and with other developers to share learning and best practices how to integrate\n' + '\n' + 'blockchain features.\n' + '\n' + 'In your role you will also provide guidance and technical leadership to less-experienced\n' + '\n' + 'team members and may be asked to assemble and perform training sessions on an as\n' + '\n' + 'needed basis. This person will also be asked to develop documentation on new or\n' + '\n' + 'existing systems.',
	  'jointeamfaq3.title': 'Backend Developer/Engineer',
	  'jointeamfaq3.subtitle': 'This full time position (40h) is required to be onsite at our Berlin office. No remote work is\n' + '\n' + 'available for the long term.\n' + '\n' + 'We are looking for an exceptional (Senior) Backend Developer/Engineer to work with\n' + '\n' + 'our cross-functional team, and join our community of talented experts.\n' + '\n' + 'About you:\n' + '\n' + 'At least 3+ years experience as a backend software engineer/developer.\n' + 'Expertise in any, one or more backend object oriented programming language\n' + '(e.g. Python, Go, Java, Scala or C++ ) and the eagerness to learn more.\n' + '\n' + 'Experience with building scalable production services.\n' + 'Track record of providing clean, concise documentation to other developers, is\n' + 'desired.\n' + '\n' + 'Experience with developing complex and scalable software systems with\n' + 'production quality deployment, monitoring and reliability.\n' + '\n' + 'Experience developing and running large scale distributed systems, service\n' + 'oriented architectures, and reliable monitoring and deployment infrastructure.',
	
