	  'update.media.3.title': 'Ares Tech 与 BigchainDB 达成战略合作，共同解决区块链游戏产业性能扩展问题！',
	  'update.media.3.description': 'Ares Tech 战略合作伙伴 BigchainDB 今天在自己的博客平台深度介绍了双方的具体合作事宜，暨将共同探索区块链游戏产业性能扩展问题的解决方案。',
	  'update.media.4.title': '「火星公开课」第166期 | Ares Tech李志超：欧洲区块链游戏产业观察\n',
	  'update.media.4.description': ' ',
