	  'introduce.dorahack.title': '本次黑客马拉松大赛以区块链应用开发为主题，旨在发掘更多的DApp创新应用。我们希望通过本次黑马赛培养与激励富有潜力的区块链游戏项目与优秀的社区开发者，共同参与Ares生态建设。',
	  'OkGaEx.title': '柏林区块链游戏平台ARES和OKGaEx达成战略合作',
