import React from 'react'
import { FormattedMessage } from 'react-intl'
import {  ButtonDropdown, DropdownToggle, DropdownMenu, DropdownItem } from 'reactstrap'
import './index.css'

const MainAresArena = () => (
  <div className="container p-3 p-md-5  main-content">
    <div className="display-table">
        <div className="table-cell">
          <div className="arena-headerLeft">
              <div className="arena-brand">
                <div className="icon-box">
                <img src="/images/icon-arena.png" width="60%"  alt=""/>
                </div>
              </div>
              <ul className="nav navbar-nav">
                <li className="AresArena-text">
                  <div>
                    <FormattedMessage id="ares"/>
                      <FormattedMessage id="arena"/>
                  </div>    
                    </li>
              </ul>
          </div>
        </div>
        </div>
    </div>
)

export default MainAresArena



