import React from 'react'
import Helmet from 'react-helmet'
import 'bootstrap/dist/css/bootstrap.min.css'
import { Container } from 'reactstrap'
import Main from '../components/Main'
import MainAresSuite from '../components/MainAresSuite'
import MainAresConnect from '../components/MainAresConnect'
import MainAresArena from '../components/MainAresArena'
import MainAboutPage from '../components/MainAboutPage'
import MainWallet from '../components/MainWallet'


 
import config from '../../data/SiteConfig'
import {
  Route
} from 'react-router-dom'
 
import '../style/custom.css'
 
const Section = ({ children, className }) => <div className={className}>{children}</div>
const routes = [{
  path: '/',
  component: Main,
}, {
  path: '/aressuite',
  component: MainAresSuite,
},
{
  path: '/aresarena',
  component: MainAresArena,
},  
{
  path: '/aresconnect',
  component: MainAresConnect,
},
{
  path: '/aressuitedocuments',
  component: MainAresSuite,
},
{
  path: '/about',
  component: MainAboutPage,
},
{
  path: '/wallet',
  component: MainWallet,
}
];

 
const MainLayout = ({ langKey, theme, footerTheme, children }) => (
 <div className="ares index-container fluid-container">
   <Helmet title={config.siteTitle} />
   <Section className={theme}>
     <Container>
     {
      routes.map(route => (
        <Route key={route.path} exact path={route.path} component={route.component} />
      ))
      }
     </Container>
     {theme == "main" &&
        <React.Fragment>
        <div className="main-shape-1">
          <img alt="main-shape-1" src="/images/main-shape-1.png" width="100%" height="100%" />
        </div>
        <div className="main-shape-2">
          <img alt="main-shape-2" src="/images/main-shape-2.png" width="100%" height="100%" />
        </div>
      </React.Fragment>
      }
   </Section>
   {children}
 </div>
)
 
export default MainLayout


