import React from 'react'
import { FormattedMessage } from 'react-intl'
import { Link } from 'react-router-dom';
import './index.css'


const MainAresSuite = () => (
  <div className="container p-3 p-md-5 main-content">
    <div className="display-table">
        <div className="table-cell">
          <div className="arena-headerLeft">
              <div className="arena-brand">
                <div className="icon-box">
                <img src="/images/icon-suite.png" width="60%"  alt=""/>
                </div>
              </div>
              <ul className="nav navbar-nav">
                <li className="aressuite-text">
                  <div className="aressuite-text-style">
                      <FormattedMessage id="ares"/>
                      <FormattedMessage id="suite"/>
                  </div>    
                </li>
              </ul>
          </div>
          <ul className="nav navbar-nav aressuite-headerRight">
              <li><Link to="#"><FormattedMessage id="start"/></Link></li>
              <li><Link to="/aressuitedocuments"><FormattedMessage id="documents"/></Link></li>
              <li><Link to="#"><FormattedMessage id="community"/></Link></li>
          </ul>
        </div>
        </div>
       
    </div>
)

export default MainAresSuite



